$imgPath: "../../img";

@import "./modules/variables";
@import "./modules/mixins";
@import "./modules/buttons";
@import "./modules/header";
@import "./modules/hero";
@import "./modules/benefits";
@import "./modules/features";
@import "./modules/conditions";
@import "./modules/tour";
@import "./modules/whatsapp";

/*

CSS STRUCTURE:

1. VARIABLES
2. GENERAL TYPOGRAPHY

--------------------------------------------------------------*/

/*  General Styles
/*----------------------------------------------*/
*,
*::before,
*::after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-decoration: none !important;
}

html {
    box-sizing: border-box;
}

body {
    --heading-font: "Roboto Condensed", sans-serif;
    --heading-font-weight: 700;
    --heading-color: #313131;
    --heading-line-height: 1.24;

    --accent-color: #d06051;
    --linear-gradient: linear-gradient(90deg, #c62813 0%, #d06051 100%);

    /* bootstrap */
    --bs-body-font-family: "Roboto", sans-serif;
    --bs-body-font-size: 18px;
    --bs-body-font-weight: 400;
    --bs-body-line-height: 1.6875rem;
    --bs-body-color: #777777;
    --bs-body-color-rgb: 143, 143, 143;

    --bs-primary: #8c907e;
    --bs-secondary: #6c757d;
    --bs-black: #313131;
    --bs-light: #ffffff;
    --bs-dark: #212529;
    --bs-gray: #d9d9d9;
    --bs-gray-dark: #51565b;

    --bs-primary-rgb: 140, 144, 126;
    --bs-secondary-rgb: 108, 117, 125;
    --bs-black-rgb: 17, 17, 17;
    --bs-light-rgb: 241, 241, 240;
    --bs-dark-rgb: 33, 37, 41;

    --bs-link-color: #111;
    --bs-link-color-rgb: 17, 17, 17;
    --bs-link-decoration: underline;
    --bs-link-hover-color: #111;
    --bs-link-hover-color-rgb: 17, 17, 17;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    font-family: var(--heading-font);
    font-weight: var(--heading-font-weight);
    color: var(--heading-color);
    line-height: var(--heading-line-height);
    letter-spacing: 0.065rem;
    text-transform: uppercase;
    font-family: "Federo", sans-serif;
}

h6 {
    font-style: italic;
    font-weight: 400;
    letter-spacing: 0.19rem;
}

.heading-color {
    color: var(--accent-color);
}

a {
    text-decoration: none;
}

/* bootstrap button style  */

/*---- navigation section style start ----*/

.navbar {
    font-family: var(--heading-font);
    --bs-navbar-padding-x: 0;
    --bs-navbar-padding-y: 0.5rem;
    --bs-navbar-color: var(--bs-black);
    --bs-navbar-hover-color: var(--accent-color);
    --bs-navbar-disabled-color: rgba(var(--bs-emphasis-color-rgb), 0.3);
    --bs-navbar-active-color: var(--accent-color);
    --bs-navbar-brand-padding-y: 0.3125rem;
    --bs-navbar-brand-margin-end: 1rem;
    --bs-navbar-brand-font-size: 1.25rem;
    --bs-navbar-brand-color: rgba(var(--bs-emphasis-color-rgb), 1);
    --bs-navbar-brand-hover-color: rgba(var(--bs-emphasis-color-rgb), 1);
}

.nav-link {
    font-size: 1rem;
    font-weight: 700;
}

nav.navbar {
    z-index: 999999;
}

/* dropdown style  */

.dropdown-menu {
    --bs-dropdown-border-radius: 0px;
}

.dropdown-item.active,
.dropdown-item:active {
    background-color: var(--bs-gray);
    color: var(--bs-black);
}

.dropdown-item {
    font-weight: 600;
    color: var(--bs-black);
}

@media only screen and (max-width: 1200px) {
    a.phone-no {
        display: none;
    }
}

@media only screen and (max-width: 1800px) {
    .pattern-overlay {
        display: none;
    }
}

.bg-secondary {
    --bs-bg-opacity: 1;
    background-color: var(--accent-color) !important;
}

/*---- hero section style start ----*/

.pattern-overlay.pattern-right {
    top: 30%;
    right: 0px;
}

form.hero-form {
    background: rgba(255, 255, 255, 0.9);
}

.form-label {
    font-size: 0.875rem;
    color: var(--bs-black);
    text-transform: uppercase;
}

.form-control {
    /* border: none; */
    border-radius: 0;
}

/*---- partner section style start ----*/

img.logo-image {
    filter: saturate(0);
    opacity: 50%;
    transition: ease-in 0.2s;
}

img.logo-image:hover {
    filter: saturate(100%);
    opacity: 1;
}

/*---- testimonial section style start ----*/

section#testimonial {
    background: linear-gradient(90deg, #c62813 0%, #d06051 100%);
}

.testimonial-pattern-overlay.pattern-right {
    top: 35%;
    right: 0px;
}

.testimonial-pattern-overlay.pattern-left {
    top: 20%;
    left: 0px;
}

.testimonial-content {
    background: #f9e9e9;
}

iconify-icon.rate {
    color: #f2b821;
}

/* swiper overide 
--------------------------------------------------------------*/

.swiper-pagination-bullet {
    background: var(--swiper-pagination-bullet-inactive-color, var(--bs-light));
}

/*---- services section style start ----*/

.services-components {
    border: 1px solid var(--accent-color);
    transition: all 0.5s ease-in;
}

iconify-icon.services-icon {
    font-size: 80px;
    color: var(--accent-color);
    transition: all 0.5s ease-in;
}

.services-heading {
    transition: all 0.5s ease-in;
}

.service-btn {
    transition: all 0.5s ease-in;
}

