  background: var(--accent-color);
  color: var(--bs-light);
}

.services-components:hover .services-heading,
.services-components:hover iconify-icon.services-icon {
  color: var(--bs-light);
}

.services-components:hover .service-btn {
  background: var(--bs-light);
  color: var(--bs-black);
  transition: all 0.5s ease-in;
} */

/*---- projects section style start ----*/

section#projects {
    background: var(--accent-color);
}

button.filter-button {
    font-family: var(--heading-font);
    border: 1px solid #fff;
    background: transparent;
    color: var(--bs-light);
    text-transform: uppercase;
    font-size: 1rem;
    transition: all 0.3s ease-in;
}

button.filter-button.active {
    color: var(--bs-dark);
    background: var(--bs-light);
}

button.filter-button:hover {
    color: var(--bs-dark);
    background: var(--bs-light);
}

.item .description {
    width: 90%;
    height: 90%;
    position: absolute;
    bottom: 20px;
    left: 15px;
    z-index: 5;
    opacity: 0;
    background: rgba(255, 255, 255, 0.9);
    transition: all 0.5s ease-in;
}

.item:hover .description {
    opacity: 1;
}

@media only screen and (min-width: 770px) and (max-width: 1400px) {
    .item .description {
        text-align: start;
    }

    .project-date,
    .project-heading {
        display: none;
    }
}

@media only screen and (min-width: 770px) and (max-width: 1200px) {
    .item .description {
        left: -5px;
        background: none;
    }
}

/*---- process section style start ----*/

.process-components {
    border-bottom: 2px dotted var(--accent-color);
    /* border-top: 2px solid var(--accent-color); */
    transition: ease-in 0.3s;
}

img.arrow {
    opacity: 0;
}

iconify-icon.process-icon {
    color: var(--accent-color);
    font-size: 110px;
}

/*---- faq section style start ----*/

/* accordian style override  */

.accordion {
    --bs-accordion-border-color: var(--accent-color);
    --bs-accordion-transition: color 0.15s ease-in-out,
        background-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out,
        border-radius 0.15s ease;
    --bs-accordion-btn-icon: url("https://api.iconify.design/ion/caret-down.svg?color=%23d06051&width=30");
    --bs-accordion-btn-active-icon: url("https://api.iconify.design/ion/caret-down.svg?color=%23d06051&width=30");
    --bs-accordion-border-radius: 0px;
}

.accordion-header {
    margin-bottom: 0;
    border-top: 1px solid var(--accent-color);
}

.accordion-button {
    font-family: var(--heading-font);
    font-weight: var(--heading-font-weight);
    color: var(--heading-color);
    line-height: var(--heading-line-height);
    letter-spacing: 0.065rem;
    text-transform: uppercase;
}

.accordion-button:focus {
    box-shadow: none;
}

.accordion-button:not(.collapsed) {
    color: var(--heading-color);
    background-color: var(--bs-light);
    box-shadow: none;
}

/*---- cta section style start ----*/

section#cta {
    background: var(--linear-gradient);
}

.cta-button {
    background: var(--bs-light);
    color: var(--bs-black);
}

.cta-button:hover,
.cta-button:active,
.cta-button:focus {
    background: var(--bs-light);
    color: var(--bs-black);
}

@media only screen and (max-width: 770px) {
    .cta-pattern-overlay {
        display: none;
    }
}

/*--------------------------------------------------------------
About page style
--------------------------------------------------------------*/

.breadcrumb-item + .breadcrumb-item::before {
    color: var(--bs-light);
}

/*--------------------------------------------------------------
  Blog page style
  --------------------------------------------------------------*/

.active > .page-link,
.page-link.active {
    background-color: var(--accent-color);
    border-color: var(--accent-color);
}

.form-control:focus {
    border-color: var(--bs-black);
    box-shadow: none;
}

/*--------------------------------------------------------------
  Price page style
  --------------------------------------------------------------*/

.plan-post {
    border: 1px solid var(--accent-color);
}

span.price-tick {
    color: var(--accent-color);
}

.price-option {
    height: 320px;
}

.recommend-price {
    background: #d06051;
}

/*--------------------------------------------------------------
  Project page style
  --------------------------------------------------------------*/

button.filter-button.gallery-btn {
    letter-spacing: 0.075rem;
    border: 1px solid var(--bs-border-color);
    background: transparent;
    color: var(--bs-black);
    text-transform: uppercase;
    font-size: 1rem;
    transition: all 0.3s ease-in;
}

button.filter-button.gallery-btn.active {
    color: var(--bs-light);
    background: var(--accent-color);
}

button.filter-button.gallery-btn:hover {
    color: var(--bs-light);
    background: var(--accent-color);
}

/*--------------------------------------------------------------
reviews section style start
--------------------------------------------------------------*/

.reviews-components {
    border: 2px solid #fafafa;
    box-shadow: 0px 12px 90px rgba(12, 12, 12, 0.06);
    border-radius: 9px;
}

iconify-icon.quote {
    font-size: 60px;
    color: var(--accent-color);
}

.rate {
    color: var(--accent-color);
}

@import "./modules/footer";
